/* Qrcode.css */

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f0f0;
}

h1 {
    font-size: 36px;
    /* Increased font size for h1 */
    margin-bottom: 30px;
    /* Increased margin */
    position: relative;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
    color: #007bff;
}

h1::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #007bff, #00c2ff);
    z-index: -1;
}

h1::after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: transparent;
    z-index: 1;
}

.qr-input {
    padding: 15px;
    /* Increased padding for input */
    font-size: 24px;
    /* Increased font size for input */
    width: 400px;
    /* Increased width */
    border: 2px solid #007bff;
    border-radius: 8px;
    /* Increased border radius */
    margin-top: 30px;
    /* Increased margin */
    text-align: center;
    outline: none;
}

.qr-input:focus {
    border-color: #0056b3;
}


/* Style the QR code */

.qr-code {
    margin-top: 30px;
    /* Increased margin */
    border: 2px solid #007bff;
    padding: 30px;
    /* Increased padding for QR code */
    display: inline-block;
    background-color: #fff;
    box-shadow: 0 0 12px rgba(0, 0, 0, 0.2);
    /* Increased box shadow */
    border-radius: 12px;
    /* Increased border radius */
}